
#modal-tos .content {
  background: rgba(255, 181, 93, 0.16);
  padding: 1rem;
}
#modal-tos .content > .tos {
    margin-bottom: 1rem;
    max-height: 24rem;
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    font-size: 0.875rem;
}
@media (min-width: 576px) {
#modal-tos .content > .tos {
        padding-right: 0.5rem;
}
}
#modal-tos .content > .tos h1 {
      font-size: 1.5rem;
      margin-bottom: 1rem;
}
#modal-tos .content > .tos h2, #modal-tos .content > .tos h3 {
      font-size: 1.25rem;
      margin-bottom: 1rem;
}
#modal-tos .content > .tos ol, #modal-tos .content > .tos ul {
      padding-left: 1.25rem;
      margin: 1rem 0;
}
#modal-tos .content > .tos ol > li, #modal-tos .content > .tos ul > li {
        margin: 0.25rem 0;
}



