
.accordion > .card > .card-body {
  max-height: 0;
  overflow: hidden;
}
.accordion > .card.active {
  padding-bottom: 1rem;
}
.accordion > .card.active > .card-head {
    padding-bottom: 0.5rem;
}
.accordion > .card.active > .card-body {
    max-height: none;
}
#modal-auth .before.accordion > .card {
  background: rgba(255, 181, 93, 0.16);
}
#modal-auth .before.accordion > .card.dark {
    background: rgba(255, 181, 93, 0.32);
}
#modal-auth .after {
  background: rgba(255, 181, 93, 0.32);
  padding: 1rem;
}
#modal-auth .note {
  font-size: 0.75rem;
  color: #707070;
}
#modal-auth .field:last-of-type {
  padding-bottom: 0.25rem;
}



