.section-title-underline-tiny {
  font-size: 0.75rem;
  line-height: 0.875rem;
}
@-webkit-keyframes marquee {
0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
}
@keyframes marquee {
0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
}
.marquee {
  position: relative;
  width: 100%;
  height: 1rem;
  line-height: 1rem;
  overflow: hidden;
}
.marquee > .content {
    position: absolute;
    display: block;
    width: auto;
    padding-left: 100%;
    white-space: nowrap;
    overflow: visible;
    -webkit-animation: marquee 8s linear infinite;
            animation: marquee 8s linear infinite;
}
.ancm-wrapper {
  position: absolute;
  top: 64px;
  left: 0;
  margin-top: 1rem;
  width: 100%;
  z-index: 1030;
}



