
@media (max-width: 960px) {
.navbar-brand.compact {
    width: 64px;
    overflow: hidden;
}
}
.navbar-button {
  display: block;
  width: 60px;
  height: 64px;
}
.navbar-button:hover {
    background: rgba(255, 255, 255, 0.25);
}
#navbar-identity {
  position: absolute;
  top: 0;
  right: 0;
  background-size: contain;
}
#navbar-identity.identity-anon {
    background-image: url(assets/identity/robot.png);
}
#navbar-identity.identity-citizen {
    background-image: url(assets/identity/human.png);
}
.el-menu {
  background: none;
}
.el-menu--horizontal .el-submenu .el-submenu__title {
  height: 64px;
  line-height: 64px;
}
.el-menu--horizontal .el-submenu > .el-menu {
  top: 68px;
}
.el-menu-item, .el-submenu__title {
  padding: 0 16px;
  color: black;
}
.el-menu--horizontal .el-submenu .el-submenu__icon-arrow {
  color: rgba(0, 0, 0, 0.65);
}
.el-menu--horizontal > .el-menu-item:hover,
.el-menu--horizontal > .el-submenu.is-active .el-submenu__title,
.el-menu--horizontal > .el-submenu:hover .el-submenu__title {
  border-bottom-color: rgba(0, 0, 0, 0.65);
}
.el-menu--horizontal .el-menu-item:hover,
.el-menu--horizontal .el-submenu__title:hover {
  background: rgba(0, 0, 0, 0.1);
}



