
.support-watchout {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 1030;
}
.support-watchout > .panel {
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.25);
    position: relative;
    display: block;
    width: 100%;
    max-width: 576px;
    padding: 0.75rem 1rem;
    margin: 0 auto;
}
.support-watchout > .panel > .text {
      padding: 0.25rem;
      text-align: center;
      font-family: "century-std", "noto-serif-patch", "source-han-serif-tc", serif;
      font-size: 1.125rem;
      font-weight: bold;
      color: rgba(0, 0, 0, 0.95);
}
.support-watchout > .panel > .text > p {
        margin: 0.25rem 0;
        line-height: 1.25;
}
@media (min-width: 576px) {
.support-watchout > .panel > .text {
          text-align: left;
}
}
.support-watchout > .panel > .button {
      padding: 0.5rem;
      text-align: center;
}
.support-watchout > .panel > .button > .grouping {
        position: relative;
        display: inline-block;
}
.support-watchout > .panel > .button > .grouping > img {
          position: absolute;
          top: -0.5rem;
          left: -1rem;
          width: 42px;
}
.support-watchout > .panel > .button > .grouping > button {
          border: none;
          border-radius: 1.5rem;
          padding: 0.75rem 2.5rem;
          font-size: 1rem;
          line-height: 1;
          background: rgba(255, 255, 255, 0.65);
          color: black;
}
.support-watchout > .panel:hover > .button > .grouping > button {
      background: rgba(255, 255, 255, 0.95);
}



